.testimonials {
  &__item {
    margin-bottom: 48px;

    &:last-child {
      margin-bottom: 0;
    }

    @include onDesktop {
      margin-bottom: 0;
    }
  }
}

.testimonial {
  &__photo-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 24px;
    margin-bottom: 28px;
    transition: 0.5s;
    
    &:hover {
      transform: scale(1.05);
    }

    @include onDesktop {
      margin-bottom: 32px;
    }

  }
  &__photo {
    width: 100%;
    grid-column: 2 / 4;
  }

  &__name {
    margin: 0 0 4px;
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    text-align: center;
    color: $colorBlue;
  }

  &__role {
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    text-align: center;
    color: #9fadc2;
    margin: 0 0 4px;
  }

  &__about {
    text-align: center;
  }
}

.link {
  color: $colorBlue;
  text-decoration: none;
  cursor: default;
  &:hover {
    text-shadow: 4px 1px 2px #e0e0e0;
  }
}
